$body: #f6f9fc;
$text: #415467;
$grey2hover: #435465;
$orange: #fb7038;
$dark: #02274a;
$darkhover: #06335e;
$bannerbg: #0a206c;
$btntext: #6c7685;
$btnhover: #c3cedd;
$inputborder: #dcdddf;
$borderhover: #abb5c3;
$inputfocus: #7e8da3;
$lightgrey: #eff1f2;
$lightgrey2: #ccced0;

// $footerbg: #02274a;
$footertitle: #9ba6b4;
$footertexthover: #959da9;
$footertextdark: #54657b;
$footertextdarker: #7a8898;
$footerline: #113455;
$footerhover: #5d6e82;

$white: #fff;
$black: #000;
$blue: #011E42;
$grey: #83929F;
$grey2: #8f959e;
$fiol: #5f43c3;
// $soc: #6B7786;

// fonts
@font-face {
	font-family: 'Graphik LC Web';
	src: url('../fonts/Graphik-Regular-Cy-Web.eot');
	src: url('../fonts/Graphik-Regular-Cy-Web.eot?#iefix') format('embedded-opentype'),
	     url('../fonts/Graphik-Regular-Cy-Web.woff') format('woff');
	font-weight:  400;
	font-style:   normal;
	font-stretch: normal;
	font-display: swap;
}
@font-face {
	font-family: 'Graphik Medium Web';
	src: url('../fonts/Graphik-Medium-Web.eot');
	src: url('../fonts/Graphik-Medium-Web.eot?#iefix') format('embedded-opentype'),
		 url('../fonts/Graphik-Medium-Web.woff')  format('woff'),
		 url('../fonts/Graphik-Medium-Web.woff2') format('woff2'),
		 url('../fonts/Graphik-Medium-Web.ttf')   format('ttf');
	font-weight:  600;
	font-style:   normal;
	font-stretch: normal;
	font-display: swap;
}
@font-face {
	font-family: 'Graphik Semibold Web';
	src: url('../fonts/Graphik-Semibold-Web.eot');
	src: url('../fonts/Graphik-Semibold-Web.eot?#iefix') format('embedded-opentype'),
		 url('../fonts/Graphik-Semibold-Web.woff')  format('woff'),
		 url('../fonts/Graphik-Semibold-Web.woff2') format('woff2'),
		 url('../fonts/Graphik-Semibold-Web.ttf')   format('ttf');
	font-weight:  600;
	font-style:   normal;
	font-stretch: normal;
	font-display: swap;
}
.Regular {
	font-family: 'Graphik LC Web', Arial;
	font-weight:  400;
	font-style:   normal;
	font-stretch: normal;
}
.Medium {
	font-family: 'Graphik Medium Web', Arial;
	font-weight:  500;
	font-style:   normal;
	font-stretch: normal;
}
.Semibold {
	font-family: 'Graphik Semibold Web', Arial;
	font-weight:  600;
	font-style:   normal;
	font-stretch: normal;
}
.black  {color: $black  !important;}
.white  {color: $white  !important;}
.orange {color: $orange !important;}
.dark   {color: $dark   !important;}
.fiol   {color: $fiol   !important;}
.fiol-underlined {border-bottom: 2px solid $fiol  !important;}
.grey-underlined {border-bottom: 1px solid $grey2 !important;}

// common
* {box-sizing: border-box}
a {
	text-decoration: none;
	transition: color .3s ease-in-out;
	transition: background-size .3s cubic-bezier(.89,.03,.21,.92);;
	background-image: linear-gradient(to left, $orange, $orange);
	background-position: 0 100%;
	background-repeat: no-repeat;
	background-size: 0% 1px;
	&:hover {
		background-size: 100% 1px;
	}
}
body {
	background-color: $body;
	color: $text;
	font-family: 'Graphik LC Web', Arial;
	font-size: 18px;
	line-height: 28px;
	margin: 0;
	position: relative;
	-moz-font-feature-settings: "liga", "kern";
	-moz-font-feature-settings:"liga=1, kern=1";
	-ms-font-feature-settings:"liga", "kern";
	-o-font-feature-settings:"liga", "kern";
	-webkit-font-feature-settings:"liga", "kern";
	font-feature-settings: "liga", "kern";
	&.sidebar-opened {overflow: hidden;}
}
img {
	max-width: 100%;
	display: block;
}
h1 {
	color: $dark;
	font-family: 'Graphik Medium Web', Arial;
	font-size: 36px;
	line-height: 40px;
	letter-spacing: -0.01em;
	font-weight: 600;
	margin: 0;
}
h2 {
	color: $dark;
	font-family: 'Graphik Semibold Web', Arial;
	font-size: 36px;
	line-height: 42px;
	letter-spacing: -0.02em;
	font-weight: 600;
	margin: 0 0 28px;
}
h3 {
	color: $dark;
	font-family: 'Graphik Medium Web', Arial;
	font-size: 22px;
	line-height: 28px;
	font-weight: 600;
}
.rubric-name {
	color: $orange;
	font-family: 'Graphik Medium Web', Arial;
	margin-bottom: 15px;
}
@media(max-width: 991px) {
	.rubric-name {
		font-size: 16px;
		line-height: 34px;
		margin-bottom: 5px;
	}
}
.bigtext {
	font-size: 22px;
	line-height: 32px;
	margin-bottom: 43px;
}
.rounded {border-radius: 5px;}
.top-rounded {border-radius: 5px 5px 0 0;}
.center {text-align: center;}
.flex {
	display: flex;
	flex-flow: row wrap;
	&.column {flex-direction: column;}
}
.threecol {
	>div, >a {
		width: calc(33.33% - 20px);
		margin: 0 15px 35px;
		&:nth-child(3n-2) {margin-left:  0;}
		&:nth-child(3n)   {margin-right: 0;}
	}
	// >a {margin-bottom: 0;}
}
@media (max-width: 991px) {
	.threecol {
		>div, >a {
			display: block;
			width: 100%;
			margin-left: 0;
			margin-right: 0;
			a {display: block;}
			img {
				width: 100%;
				height: auto;
			}
		}
	}
}
.grid {display: grid;}
.btn {
	background: $dark;
	color: $btntext !important;
	padding: 11px 20px 14px 20px;
	border-radius: 5px;
	border: none;
	font-size: 18px;
	line-height: 20px;
	text-align: center;
	transition: all .3s ease-in-out;
	cursor: pointer;
	&:hover {
		background: $darkhover;
		color: $btnhover !important;
	}
	&.grey {
		background: $lightgrey;
		color: $footertexthover;
		&:hover {
			background: $darkhover;
			color: $btnhover !important;
		}
	}
	&.btn-transparent {
		background: transparent;
		color: $text;
		font-family: 'Graphik Medium Web', Arial;
		border: 1px solid $lightgrey2;
		border-radius: 5px;
		padding: 10px 20px 11px;
		margin-bottom: 20px;
		&:hover {
			border: 1px solid $borderhover;
			color: $btntext !important;
		}
		&::after {
			content: url('../img/openlink.png');
			display: inline-block;
			vertical-align: middle;
			margin-left: 14px;
		}
	}
}
.reset-btn {
	display: inline-block;
	padding: 13px 14px 12px;
	font-size: 16px;
	line-height: 16px;
	border-radius: 50%;
	border: 1px solid $lightgrey;
	margin-left: 20px;
	color: rgba($dark,.6);
	&:hover {color: $orange;}
}

.bgwhite {
	background: $white;
}
ul {
	padding: 0;
	list-style: none;
	li {
		display: flex;
		&::before {
			content: '';
			width:  8px;
			height: 8px;
			border-radius: 50%;
			margin: 11px 16px 0 0;
			flex-shrink: 0;
		}
		> ul {
			position: relative;
			&::before {
				content: '';
				position: absolute;
				left: -25px;
				width: 20px;
				height: 100%;
				background: #fff;
			}
		}
	}
	&.orange li::before {background: $orange;}
	&.dark   li::before {background: $dark;}
	&.separated li {margin-bottom: 1rem;}
	&.orange, &.dark {li {color: $text !important;}}
}

// adaptive
.wrap {
	width: 100%;
	max-width: 1200px;
	margin: 0 auto;
	overflow: hidden;
}
@media (max-width: 1380px) {
	.wrap {
		margin: 0 90px;
		width: auto;
	}
	// h1 {
	// 	font-size: 36px;
	// 	line-height: 46px;
	// }
	h2 {
		font-size: 26px;
		line-height: 30px;
	}
}
@media (max-width: 991px) {
	.wrap {margin: 0 50px;}
	// h1 {
	// 	font-size: 36px;
	// 	line-height: 46px;
	// }
	.bigtext {
		font-size: 20px !important;
		line-height: 28px !important;
	}
	p, body {
		font-size: 16px;
		line-height: 24px;
	}
}
@media (max-width: 767px) {
	.wrap {margin: 0 20px;}
	h1 {
		font-size: 33px;
		line-height: 40px;
	}
}
@media (max-width: 640px) {
	h1 {
		font-size: 28px;
		line-height: normal;
	}
}
.nobanner {margin-top: 107px;}
@media (max-width: 640px) {
	.nobanner {margin-top: 90px;}
}

// HEADER  ===============================================================================
header {
	background: transparent;
	color: $white;
	position: fixed;
	top: 0;
	width: 100%;
	padding: 11px 0;
	overflow: hidden;
	transition: top .4s ease;
	z-index: 5;
	&.movedOut {
		top: -70px;
	}
	&.has_bg {
		background: white;
		nav a {
			color: $dark;
			font-family: 'Graphik Medium Web', Arial;
		}
		.logo,
		.sidebar-toggle {
			.svg-icon {fill: $dark;}
		}
		.tel {
			color: $grey2;
			// border: 1px solid rgba($grey,.3);
			// background: url('../img/tel.png') no-repeat 13px 10px;
			&:hover{
				color: $text;
				background-color: $lightgrey;
			}
		}
	}
	&.hasno_bg {
		background: transparent;
		nav a {color: $white;}
		.logo,
		.sidebar-toggle {
			.svg-icon {fill: $white;}
		}
		.tel {
			color: $white;
			// border: 1px solid rgba($white,.3);
			// background: url('../img/tel.png') no-repeat 13px 10px;
		}
	}
	.wrap {
		display: flex;
		align-items: center;
		// justify-content: space-between;
	}
	.logo {
		background-size: 0 1px;
		.svg-icon {
			vertical-align: middle;
			width: 185px;
			height: 45px;
			fill: $white;
		}
	}
	nav {
		margin-left: auto;
		margin-right: 24px;
		// margin-right: 42px;
		// width: calc(66.66% - 216px);
		display: flex;
		justify-content: space-between;
		a {
			color: $white;
			font-size: 16px;
			font-family: 'Graphik Medium Web', Arial;
			// padding: 10px 0;
			margin: 8px 16px 12px;
			white-space: nowrap;
			transition: background-size .3s cubic-bezier(.89,.03,.21,.92);;
			background-image: linear-gradient(to left, $orange, $orange);
			background-position: 0 100%;
			background-repeat: no-repeat;
			background-size: 0% 1px;
			&:hover {
				background-size: 100% 1px;
			}
		}
	}
	.tel {
		color: $white;
		font-size: 15px;
		background-size: 0 1px;
		border-radius: 5px;
		padding: 5px 16px;
		// padding: 5px 13px 5px 40px;
		// background: url('../img/tel.png') no-repeat 13px 10px;
		white-space: nowrap;
		opacity: .6;
		cursor: pointer;
		transition: all .3s ease-in-out;
		&:hover {
			background-color: rgba(255,255,255,.15);
			color: $body;
			opacity: 1;
		}
	}
	.sidebar-toggle {
		display: none;
		margin: 2px -7px 0 auto;
		cursor: pointer;
		.svg-icon {
			padding: 8px 7px 6px;
			width: 34px;
			height: 25px;
			fill: $white;
		}
	}
}

// моб меню

.sidebar.is-active {
	visibility: visible;
}
.sidebar {
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100vh;
	visibility: hidden;
	z-index: 100;
}
.sidebar-overlay {
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	background: rgba($text,.85);
}
.sidebar-content {
	height: 100%;
	min-width: 320px;
	width: 383px;
	max-width: 100%;
	position: absolute;
	right: 0;
	top: 0;
	transform: translate3d(100%,0,0);
	transition: .8s cubic-bezier(.19,1,.22,1);	
	background: $dark;
	color: $white;
	padding: 10px 40px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	.logo {
		padding-top: 2px;
		margin: 0 0 35px;
		&:hover {background-size: 0 1px;}
		.svg-icon {
			width: 185px;
			height: 45px;
			fill: #fff;
		}
	}
	a {
		color: $white;
		font-size: 24px;
		// text-transform: lowercase;
		margin-left: 0;
		padding: 14px 0 7px;
		transition: background-size .3s cubic-bezier(.89,.03,.21,.92);;
		background-image: linear-gradient(to left, $orange, $orange);
		background-position: 0 100%;
		background-repeat: no-repeat;
		background-size: 0% 1px;
		&:hover {
			background-size: 100% 1px;
		}
		&.smalltext {
			color: $footertextdark;
			background-image: linear-gradient(to left, $footerhover, $footerhover);
			padding-top: 45px;
			+ .smalltext {padding-top: 5px;}
		}
	}
	.smalltext {
		font-size: 17px;
		padding: 0 0 3px;
	}
	.social {
		padding-top: 25px;
		display: flex;
		.svg-icon {
			width: 25px;
			height: 25px;
		}
		a {
			margin-right: 30px;
			position: relative;
			padding: 0;
			background-size: 0 1px;
			// img {transition: opacity .3s ease-in-out, transform 1s ease;}
			img {transition: opacity .3s ease-in-out;}
			.logo-white {
				position: relative;
				// transform: rotateY(.1deg);
				z-index: 1;
			}
			.logo-color {
				position: absolute;
				left: 0;
				bottom: 0px;
				opacity: 0;
				// transform: rotateY(90deg);
				z-index: 0;
			}
			&:hover {
				.logo-white {
					opacity: 0;
					// transform: rotateY(90deg);
				}
				.logo-color {
					opacity: 1;
					// transform: rotateY(.1deg);
				}
			}
		}
	}
	.sidebar-close {
		position: absolute;
		top: 30px;
		right: 40px;
		cursor: pointer;
		.svg-icon {
			display: block;
			width: 14px;
			height: 14px;
			fill: $footertextdarker;
			transition: all .3s ease-in-out;
		}
		&:hover {
		.svg-icon {fill: $footertexthover;}
		}
	}
}
.is-active .sidebar-content {
	transform: translateZ(0);
}

