// mainpage  ====================================================================
.banner {
	position: relative;
	overflow: hidden;
	height: 0;
	.banner-img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 1;
		img {max-width: none;}
		&::before {
			content: "";
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: linear-gradient(70deg, rgba(0,0,0,0) 50%, rgb(241, 88, 29) 100%);
			z-index: 1;
		}
		.svg {
			fill: $body;
			position: absolute;
			bottom: -2px;
			left: -3px;
			right: -1px;
			z-index: 2;
		}
	}
	img {
		width: 101%;
		&:not(.svg) {
			position: relative;
			left: 50%;
			transform: translateX(-50%);
		}
	}
	.banner-intro {
		position: absolute;
		top: 0;
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		z-index: 2;
		h1 {
			font-family: 'Graphik Medium Web', Arial;
			font-size: 50px;
			line-height: normal;
			letter-spacing: -0.01em;
			font-weight: 600;
			margin: 0;		
		}
	}
	.wrap {
		margin-top: 67px;
		margin-bottom: 60px;
	}
	.title {
		max-width: 600px;
		margin-left: 0;
	}
	p {
		max-width: 400px;
		font-size: 26px;
		line-height: 36px;
	}
}
@media (max-width: 1200px) {
	.banner .banner-intro h1 {
		font-size: 44px;
	}
}
@media (max-width: 1020px) {
	.banner {
		padding-top: 421px !important;
		.banner-img {
			height: 421px !important;
		}
		img:not(.svg) {
			max-width: none;
			width: auto;
			height: 100%;
		}
		.wrap {width: auto;}
		.title {max-width: 50%;}
			p {
				max-width: 70%;
				font-size: 18px;
				line-height: 28px;
			}
	}
}
@media (max-width: 991px) {
	.banner .banner-intro h1 {
		font-size: 36px;
		line-height: 46px;
	}
}
@media (max-width: 800px) {
	.banner {
