// about -------------------------------------------------------------------
.flex{
	.text {
		width: 50%;
		padding-right: 65px;
	}
	p {
		-webkit-hyphens: auto;
		-ms-hyphens: auto;
		hyphens: auto;
	}
	.pic {
		width: 50%;
		display: flex;
		justify-content: center;
