		width: calc(66.66% - 10px);
		// max-width: 750px;
		margin-left: auto;
		padding-left: 0;
		p  {max-width: 400px;}
		input::placeholder, 
		textarea::placeholder, 
		.hhagree > div {color: rgba($dark,.6);}
	}
	&.background2 {
		background: url('../img/feedback2.jpg') no-repeat 50% / cover;
		.wrap {padding: 60px 0 30px;}
	}
	&.background3 {
		background: url('../img/feedback3.jpg') no-repeat 0% / cover;
	}
	.attach {
		position: relative;
		input {
			position: relative;
			opacity: 0;
			z-index: 3;
		}
		label {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			// height: 48px;
			border: 1px solid $inputborder;
			border-radius: 5px;
			background: #fff;
			color: rgba($dark,.3);
			font-family: 'Graphik LC Web', Arial;
			font-size: 14px;
			line-height: 20px;
			padding: 13px 90px 15px 14px;
			text-overflow: ellipsis;
			overflow: hidden;
			white-space: nowrap;
			transition: all .3s ease-in-out;
			z-index: 1;
		}
		&::after {
			content: url('../img/paper-clip.png');
			display: block;
			position: absolute;
			right: 0;
			top: 0;
			line-height: 23px;
			padding: 14px 34px 7px;
			// height: 48px;
			width: 90px;
			background: $dark;
			border-radius: 0 5px 5px 0;
			box-sizing: border-box;
			transition: all .3s ease-in-out;
			z-index: 2;
		}
		&:hover {
			&::after {
				content: url('../img/paper-clip-hover.png');
				background: $darkhover;
			}
		}
	}
}
