			font-weight: 600;
			border: 1px solid $lightgrey2;
			border-radius: 5px;
			padding: 9px 16px;
			white-space: nowrap;
			cursor: pointer;
			transition: all .3s ease-in-out;
			text-align: center;
			margin: 15px 0 20px;
			background-size: 0 1px;
			&:hover {
				color: $btntext;
				border-color: $borderhover;
			}
