		display: block;
		max-width: 300px;
		margin: 63px 0 30px;
	}
}
.filter-overlay {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
}
.filter {
	position: relative;
	margin-bottom: 20px;
	width: 210px;
	.current {
		color: rgba($dark,.6);
		transition: all .3s ease-in-out;
		// opacity: .6;
		font-size: 16px;
		line-height: 48px;
		padding: 0 35px 0 30px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		cursor: pointer;
		&::after {
			content: '';
			width: 7px;
			height: 7px;
			position: absolute;
			right: 20px;
			top: 20px;
			// margin: 0 0 3px 20px;
			border-right: 1px solid rgba($dark,.6);
			border-bottom: 1px solid rgba($dark,.6);
			transform: rotate(45deg);
			transform-origin: 70% 70%;
			transition: all .2s ease-in-out;
		}
		&:hover, &.opened {
			color: $grey2hover;
			// border: 1px solid $borderhover;
			&::after {
				border-right: 1px solid $grey2hover;
				border-bottom: 1px solid $grey2hover;
			}
		}
		&.opened::after{
			transform: rotate(-135deg);
		}
	}
	.select {
		background: $white;
		color: rgba($dark,.6);
		opacity: 0;
		position: absolute;
		top: 48px;
		list-style: none;
		display: flex;
		flex-direction: column;
		border: 1px solid $lightgrey2;
		border-radius: 5px;
		padding: 12px 3px 20px;
		margin: 16px 0 0;
		width: 210px;
		transition: all .1s ease-in-out;
		opacity: 0;
		z-index: -1;
		&:hover {
			border-color: $borderhover;
		}
		&.opened {
			opacity: 1;
			z-index: 1;
		}
		.option {
			padding: 12px 26px;
			font-size: 16px;
			line-height: normal;
			transition: all .3s ease-in-out;
			cursor: pointer;
			&::before {display: none;}
			&.selected {
				background: $lightgrey;
				color: $dark;
			}
			&:hover {
				background: $lightgrey;
				color: $grey2hover;
			}
		}
	}
}
.partners {
	background: $white;
	padding-bottom: 95px;
	.filter +.flex {
		justify-content: space-between;
		&::after {
			content: "";
			flex-basis: calc(33.33% - 15px);
		}
	}
}
.partner {
	position: relative;
	width: calc(33% - 15px);
	height: 240px;
	padding: 30px;
	border-radius: 5px;
	border: 1px solid $lightgrey2;
	margin-bottom: 30px;
	transition: all .3s ease-in-out;
	&:hover{border-color: $borderhover;}
	>div {
		&:first-child {
			height: 90px;
			display: flex;
			align-items: center;
			justify-content: flex-start;
			>div{
				width: 100%;
				max-height: 100%;
			}
			img {max-height: 100%;}
		}
		&:nth-child(2) {
			position: absolute;
			bottom: 27px;
		}
	}
	.name {
		color: $dark;
	}
	.status {
		font-size: 14px;
		color: $grey2;
	}
}
@media(max-width: 810px) {
	.partner {
		width: calc(50% - 15px);
	}
}
@media(max-width: 640px) {
