				border-top-color: $footertexthover;
				border-right-color: $footertexthover;
				transform: rotate(45deg);
				transform-origin: 65% 75%;
				transition: all .3s ease-in-out;
				cursor: pointer;
			}
			.search-text {
				width: 100%;
				height: 100%;
				font-size: 16px;
				font-family: 'Graphik LC Web', Arial;
				padding: 10px 0;
				border: none;
				color: rgba($dark,.6);
				transition: color .3s ease-in-out;
				&.focus, &:focus-visible {outline: none;}
			}
			&:hover, &.focus {
				.search-text {color: rgba($dark,.4);}
