	}
}
.card {
	background-size: 0 1px !important;
	border-radius: 5px;
	box-shadow: 0 1px 20px rgba(#d2d3d1,.75);
	margin-bottom: 35px;
	>a, .img {
		background: $body;
		position: relative;
		height: 0;
		padding-top: calc(320 / 585 *100%);
		overflow: hidden;
		border-radius: 5px 5px 0 0;
	}
	img {
		border-radius: 5px 5px 0 0;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		transition: transform .2s cubic-bezier(0,.6,.25,1);
	}
	.anonce {
		background-color: $white;
		border-top: 1px solid $white !important;
		border-radius: 0 0 5px 5px;
		flex-grow: 1;
		padding: 15px 30px 23px;
	}
	.type {
		color: $footertexthover;
		font-size: 12px;
		line-height: 40px;
		letter-spacing: .5px;
		text-transform: uppercase;
	}
	.title {
		color: $dark;
		font-size: 18px;
		line-height: normal;
		margin-bottom: 12px;
		span {
			padding-bottom: 2px;
			transition: background-size .3s cubic-bezier(.89,.03,.21,.92);;
			background-image: linear-gradient(to left, $orange, $orange);
			background-position: 0 100%;
			background-repeat: no-repeat;
			background-size: 0% 1px;				
			transition: all .3s ease-in-out;
		}
	}
	.date {
		color: $footertexthover;
		font-size: 14px;
		line-height: 40px;
		margin-top: auto;
	}
	.descr {
		color: $footertexthover;
		font-size: 14px;
		line-height: 18px;
	}
}
a.card:hover {
	img {transform: scale(1.03);}
	.title {
		background-size: 0 1px;
		span {background-size: 100% 1px;}
	}
}
.paginator {
	padding: 49px 0;
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: calc(33% - 15px);
	margin: auto;
}
.arrowlink {
	border: 1px solid $footertexthover;
	border-radius: 50%;
	padding: 21px;
	text-align: center;
	display: inline-block;
	transition: all .3s ease-in-out;
	&.disabled {
		background-size: 0 1px;
		&.prev::before {
			border-left-color:   $footertexthover;
			border-bottom-color: $footertexthover;	
		}
		&.next::before {
			border-top-color:   $footertexthover;
			border-right-color: $footertexthover;
		}
	}
	&::before {
		content: '';
		display: block;
		width: 10px;
		height: 10px;
		border: 2px solid transparent;
		transform: rotate(45deg);
		transition: all .3s ease-in-out;
	}
	&.prev::before {
		border-left-color:   $orange;
		border-bottom-color: $orange;
		transform-origin: 65% 75%;
	}
	&.next::before {
		border-top-color:   $orange;
		border-right-color: $orange;
		transform-origin: 45% 25%;
	}
	&:hover:not(.disabled) {
		background: $orange;
		border-color: transparent;
		&.prev::before {
			border-left-color:   $white;
			border-bottom-color: $white;
		}
		&.next::before {
			border-top-color:   $white;
			border-right-color: $white;
		}	
	}
}

@media(max-width: 991px) {
