footer {
	padding: 75px 0 18px;
	background: $dark url('../img/footer-right.png') no-repeat top right;
	color: $footertextdarker;
	a {
		// display: block;
		color: $footertextdarker;
		padding-bottom: 4px;
		transition: color .3s ease-in-out, background-size .3s cubic-bezier(.89,.03,.21,.92);;
		background-image: linear-gradient(to left, $footerhover, $footerhover);
		background-position: 0 100%;
		background-repeat: no-repeat;
		background-size: 0% 1px;
		&:hover {
			background-size: 100% 1px;
			color: $footertexthover;
