	a {
		font-size: 16px;
		line-height: 24px;
		border-bottom: none;
		&:hover {
			color: $text;
		}
	}
	button {
		font-family: 'Graphik LC Web', Arial;
		font-size: 18px;
		padding: 12px 84px 15px;
		color: $btntext;
		&:hover {
			background: $darkhover;
			color: $btnhover;
		}
	}
}
@media (max-width: 1380px) {
	#gdpr-cookie-message {
		max-width: calc(100% - 180px);
	}
}
@media (max-width: 991px) {
	#gdpr-cookie-message {
		max-width: calc(100% - 100px);
	}
}
@media (max-width: 767px) {
	#gdpr-cookie-message {
		max-width: calc(100% - 40px);
		padding: 15px 20px !important;
	}
}
@media (max-width: 640px) {
	#gdpr-cookie-message {
		flex-direction: column;
		p {
			text-align: center;
			&:first-child {padding-right: 0}
		}
		button {
			padding: 12px 74px 15px;
			margin-top: 20px;
			margin-left: 0;
		}
	}
}