@font-face {
  font-family: 'Graphik LC Web';
  src: url("../fonts/Graphik-Regular-Cy-Web.eot");
  src: url("../fonts/Graphik-Regular-Cy-Web.eot?#iefix") format("embedded-opentype"), url("../fonts/Graphik-Regular-Cy-Web.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Graphik Medium Web';
  src: url("../fonts/Graphik-Medium-Web.eot");
  src: url("../fonts/Graphik-Medium-Web.eot?#iefix") format("embedded-opentype"), url("../fonts/Graphik-Medium-Web.woff") format("woff"), url("../fonts/Graphik-Medium-Web.woff2") format("woff2"), url("../fonts/Graphik-Medium-Web.ttf") format("ttf");
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Graphik Semibold Web';
  src: url("../fonts/Graphik-Semibold-Web.eot");
  src: url("../fonts/Graphik-Semibold-Web.eot?#iefix") format("embedded-opentype"), url("../fonts/Graphik-Semibold-Web.woff") format("woff"), url("../fonts/Graphik-Semibold-Web.woff2") format("woff2"), url("../fonts/Graphik-Semibold-Web.ttf") format("ttf");
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  font-display: swap;
}

.Regular {
  font-family: 'Graphik LC Web', Arial;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
}

.Medium {
  font-family: 'Graphik Medium Web', Arial;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
}

.Semibold {
  font-family: 'Graphik Semibold Web', Arial;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
}

.black {
  color: #000 !important;
}

.white {
  color: #fff !important;
}

.orange {
  color: #fb7038 !important;
}

.dark {
  color: #02274a !important;
}

.fiol {
  color: #5f43c3 !important;
}

.fiol-underlined {
  border-bottom: 2px solid #5f43c3 !important;
}

.grey-underlined {
  border-bottom: 1px solid #8f959e !important;
}

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  transition: color .3s ease-in-out;
  transition: background-size 0.3s cubic-bezier(0.89, 0.03, 0.21, 0.92);
  background-image: linear-gradient(to left, #fb7038, #fb7038);
  background-position: 0 100%;
  background-repeat: no-repeat;
  background-size: 0% 1px;
}

a:hover {
  background-size: 100% 1px;
}

body {
  background-color: #f6f9fc;
  color: #415467;
  font-family: 'Graphik LC Web', Arial;
  font-size: 18px;
  line-height: 28px;
  margin: 0;
  position: relative;
  -moz-font-feature-settings: "liga", "kern";
  -moz-font-feature-settings: "liga=1, kern=1";
  -ms-font-feature-settings: "liga", "kern";
  -o-font-feature-settings: "liga", "kern";
  -webkit-font-feature-settings: "liga", "kern";
  font-feature-settings: "liga", "kern";
}

body.sidebar-opened {
  overflow: hidden;
}

img {
  max-width: 100%;
  display: block;
}

h1 {
  color: #02274a;
  font-family: 'Graphik Medium Web', Arial;
  font-size: 36px;
  line-height: 40px;
  letter-spacing: -0.01em;
  font-weight: 600;
  margin: 0;
}

h2 {
  color: #02274a;
  font-family: 'Graphik Semibold Web', Arial;
  font-size: 36px;
  line-height: 42px;
  letter-spacing: -0.02em;
  font-weight: 600;
  margin: 0 0 28px;
}

h3 {
  color: #02274a;
  font-family: 'Graphik Medium Web', Arial;
  font-size: 22px;
  line-height: 28px;
  font-weight: 600;
}

.rubric-name {
  color: #fb7038;
  font-family: 'Graphik Medium Web', Arial;
  margin-bottom: 15px;
}

@media (max-width: 991px) {
  .rubric-name {
    font-size: 16px;
    line-height: 34px;
    margin-bottom: 5px;
  }
}

.bigtext {
  font-size: 22px;
  line-height: 32px;
  margin-bottom: 43px;
}

.rounded {
  border-radius: 5px;
}

.top-rounded {
  border-radius: 5px 5px 0 0;
}

.center {
  text-align: center;
}

.flex {
  display: flex;
  flex-flow: row wrap;
}

.flex.column {
  flex-direction: column;
}

.threecol > div, .threecol > a {
  width: calc(33.33% - 20px);
  margin: 0 15px 35px;
}

.threecol > div:nth-child(3n-2), .threecol > a:nth-child(3n-2) {
  margin-left: 0;
}

.threecol > div:nth-child(3n), .threecol > a:nth-child(3n) {
  margin-right: 0;
}

@media (max-width: 991px) {
  .threecol > div, .threecol > a {
    display: block;
    width: 100%;
    margin-left: 0;
    margin-right: 0;
  }
  .threecol > div a, .threecol > a a {
    display: block;
  }
  .threecol > div img, .threecol > a img {
    width: 100%;
    height: auto;
  }
}

.grid {
  display: grid;
}

.btn {
  background: #02274a;
  color: #6c7685 !important;
  padding: 11px 20px 14px 20px;
  border-radius: 5px;
  border: none;
  font-size: 18px;
  line-height: 20px;
  text-align: center;
  transition: all .3s ease-in-out;
  cursor: pointer;
}

.btn:hover {
  background: #06335e;
  color: #c3cedd !important;
}

.btn.grey {
  background: #eff1f2;
  color: #959da9;
}

.btn.grey:hover {
  background: #06335e;
  color: #c3cedd !important;
}

.btn.btn-transparent {
  background: transparent;
  color: #415467;
  font-family: 'Graphik Medium Web', Arial;
  border: 1px solid #ccced0;
  border-radius: 5px;
  padding: 10px 20px 11px;
  margin-bottom: 20px;
}

.btn.btn-transparent:hover {
  border: 1px solid #abb5c3;
  color: #6c7685 !important;
}

.btn.btn-transparent::after {
  content: url("../img/openlink.png");
  display: inline-block;
  vertical-align: middle;
  margin-left: 14px;
}

.reset-btn {
  display: inline-block;
  padding: 13px 14px 12px;
  font-size: 16px;
  line-height: 16px;
  border-radius: 50%;
  border: 1px solid #eff1f2;
  margin-left: 20px;
  color: rgba(2, 39, 74, 0.6);
}

.reset-btn:hover {
  color: #fb7038;
}

.bgwhite {
  background: #fff;
}

ul {
  padding: 0;
  list-style: none;
}

ul li {
  display: flex;
}

ul li::before {
  content: '';
  width: 8px;
  height: 8px;
  border-radius: 50%;
  margin: 11px 16px 0 0;
  flex-shrink: 0;
}

ul li > ul {
  position: relative;
}

ul li > ul::before {
  content: '';
  position: absolute;
  left: -25px;
  width: 20px;
  height: 100%;
  background: #fff;
}

ul.orange li::before {
  background: #fb7038;
}

ul.dark li::before {
  background: #02274a;
}

ul.separated li {
  margin-bottom: 1rem;
}

ul.orange li, ul.dark li {
  color: #415467 !important;
}

.wrap {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}

@media (max-width: 1380px) {
  .wrap {
    margin: 0 90px;
    width: auto;
  }
  h2 {
    font-size: 26px;
    line-height: 30px;
  }
}

@media (max-width: 991px) {
  .wrap {
    margin: 0 50px;
  }
  .bigtext {
    font-size: 20px !important;
    line-height: 28px !important;
  }
  p, body {
    font-size: 16px;
    line-height: 24px;
  }
}

@media (max-width: 767px) {
  .wrap {
    margin: 0 20px;
  }
  h1 {
    font-size: 33px;
    line-height: 40px;
  }
}

@media (max-width: 640px) {
  h1 {
    font-size: 28px;
    line-height: normal;
  }
}

.nobanner {
  margin-top: 107px;
}

@media (max-width: 640px) {
  .nobanner {
    margin-top: 90px;
  }
}

header {
  background: transparent;
  color: #fff;
  position: fixed;
  top: 0;
  width: 100%;
  padding: 11px 0;
  overflow: hidden;
  transition: top .4s ease;
  z-index: 5;
}

header.movedOut {
  top: -70px;
}

header.has_bg {
  background: white;
}

header.has_bg nav a {
  color: #02274a;
  font-family: 'Graphik Medium Web', Arial;
}

header.has_bg .logo .svg-icon,
header.has_bg .sidebar-toggle .svg-icon {
  fill: #02274a;
}

header.has_bg .tel {
  color: #8f959e;
}

header.has_bg .tel:hover {
  color: #415467;
  background-color: #eff1f2;
}

header.hasno_bg {
  background: transparent;
}

header.hasno_bg nav a {
  color: #fff;
}

header.hasno_bg .logo .svg-icon,
header.hasno_bg .sidebar-toggle .svg-icon {
  fill: #fff;
}

header.hasno_bg .tel {
  color: #fff;
}

header .wrap {
  display: flex;
  align-items: center;
}

header .logo {
  background-size: 0 1px;
}

header .logo .svg-icon {
  vertical-align: middle;
  width: 185px;
  height: 45px;
  fill: #fff;
}

header nav {
  margin-left: auto;
  margin-right: 24px;
  display: flex;
  justify-content: space-between;
}

header nav a {
  color: #fff;
  font-size: 16px;
  font-family: 'Graphik Medium Web', Arial;
  margin: 8px 16px 12px;
  white-space: nowrap;
  transition: background-size 0.3s cubic-bezier(0.89, 0.03, 0.21, 0.92);
  background-image: linear-gradient(to left, #fb7038, #fb7038);
  background-position: 0 100%;
  background-repeat: no-repeat;
  background-size: 0% 1px;
}

header nav a:hover {
  background-size: 100% 1px;
}

header .tel {
  color: #fff;
  font-size: 15px;
  background-size: 0 1px;
  border-radius: 5px;
  padding: 5px 16px;
  white-space: nowrap;
  opacity: .6;
  cursor: pointer;
  transition: all .3s ease-in-out;
}

header .tel:hover {
  background-color: rgba(255, 255, 255, 0.15);
  color: #f6f9fc;
  opacity: 1;
}

header .sidebar-toggle {
  display: none;
  margin: 2px -7px 0 auto;
  cursor: pointer;
}

header .sidebar-toggle .svg-icon {
  padding: 8px 7px 6px;
  width: 34px;
  height: 25px;
  fill: #fff;
}

.sidebar.is-active {
  visibility: visible;
}

.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  visibility: hidden;
  z-index: 100;
}

.sidebar-overlay {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: rgba(65, 84, 103, 0.85);
}

.sidebar-content {
  height: 100%;
  min-width: 320px;
  width: 383px;
  max-width: 100%;
  position: absolute;
  right: 0;
  top: 0;
  transform: translate3d(100%, 0, 0);
  transition: 0.8s cubic-bezier(0.19, 1, 0.22, 1);
  background: #02274a;
  color: #fff;
  padding: 10px 40px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.sidebar-content .logo {
  padding-top: 2px;
  margin: 0 0 35px;
}

.sidebar-content .logo:hover {
  background-size: 0 1px;
}

.sidebar-content .logo .svg-icon {
  width: 185px;
  height: 45px;
  fill: #fff;
}

.sidebar-content a {
  color: #fff;
  font-size: 24px;
  margin-left: 0;
  padding: 14px 0 7px;
  transition: background-size 0.3s cubic-bezier(0.89, 0.03, 0.21, 0.92);
  background-image: linear-gradient(to left, #fb7038, #fb7038);
  background-position: 0 100%;
  background-repeat: no-repeat;
  background-size: 0% 1px;
}

.sidebar-content a:hover {
  background-size: 100% 1px;
}

.sidebar-content a.smalltext {
  color: #54657b;
  background-image: linear-gradient(to left, #5d6e82, #5d6e82);
  padding-top: 45px;
}

.sidebar-content a.smalltext + .smalltext {
  padding-top: 5px;
}

.sidebar-content .smalltext {
  font-size: 17px;
  padding: 0 0 3px;
}

.sidebar-content .social {
  padding-top: 25px;
  display: flex;
}

.sidebar-content .social .svg-icon {
  width: 25px;
  height: 25px;
}

.sidebar-content .social a {
  margin-right: 30px;
  position: relative;
  padding: 0;
  background-size: 0 1px;
}

.sidebar-content .social a img {
  transition: opacity .3s ease-in-out;
}

.sidebar-content .social a .logo-white {
  position: relative;
  z-index: 1;
}

.sidebar-content .social a .logo-color {
  position: absolute;
  left: 0;
  bottom: 0px;
  opacity: 0;
  z-index: 0;
}

.sidebar-content .social a:hover .logo-white {
  opacity: 0;
}

.sidebar-content .social a:hover .logo-color {
  opacity: 1;
}

.sidebar-content .sidebar-close {
  position: absolute;
  top: 30px;
  right: 40px;
  cursor: pointer;
}

.sidebar-content .sidebar-close .svg-icon {
  display: block;
  width: 14px;
  height: 14px;
  fill: #7a8898;
  transition: all .3s ease-in-out;
}

.sidebar-content .sidebar-close:hover .svg-icon {
  fill: #959da9;
}

.is-active .sidebar-content {
  transform: translateZ(0);
}

@media (max-width: 1360px) {
  header nav {
    margin-right: 0;
  }
}

@media (max-width: 1295px) {
  header nav a {
    margin-left: 10px;
    margin-right: 10px;
  }
}

@media (max-width: 1240px) {
  header nav, header .tel {
    display: none;
  }
  header .sidebar-toggle {
    display: block;
  }
}

@media (max-width: 991px) {
  header .tel {
    font-size: 14px;
    padding: 1px 12px 1px 36px;
    background-position-x: 10px;
    background-position-y: 7px;
  }
}

@media (max-width: 640px) {
  header .logo .svg-icon, .sidebar-content .logo .svg-icon {
    width: 148px;
    height: 36px;
  }
  header .tel, .sidebar-content .tel {
    position: absolute;
    right: -130px;
  }
  .sidebar-content {
    padding: 8px 20px;
  }
  .sidebar-content .sidebar-close {
    top: 24px;
    right: 22px;
  }
}

.banner {
  position: relative;
  overflow: hidden;
  height: 0;
}

.banner .banner-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.banner .banner-img img {
  max-width: none;
}

.banner .banner-img::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(70deg, rgba(0, 0, 0, 0) 50%, #f1581d 100%);
  z-index: 1;
}

.banner .banner-img .svg {
  fill: #f6f9fc;
  position: absolute;
  bottom: -2px;
  left: -3px;
  right: -1px;
  z-index: 2;
}

.banner img {
  width: 101%;
}

.banner img:not(.svg) {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.banner .banner-intro {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 2;
}

.banner .banner-intro h1 {
  font-family: 'Graphik Medium Web', Arial;
  font-size: 50px;
  line-height: normal;
  letter-spacing: -0.01em;
  font-weight: 600;
  margin: 0;
}

.banner .wrap {
  margin-top: 67px;
  margin-bottom: 60px;
}

.banner .title {
  max-width: 600px;
  margin-left: 0;
}

.banner p {
  max-width: 400px;
  font-size: 26px;
  line-height: 36px;
}

@media (max-width: 1200px) {
  .banner .banner-intro h1 {
    font-size: 44px;
  }
}

@media (max-width: 1020px) {
  .banner {
    padding-top: 421px !important;
  }
  .banner .banner-img {
    height: 421px !important;
  }
  .banner img:not(.svg) {
    max-width: none;
    width: auto;
    height: 100%;
  }
  .banner .wrap {
    width: auto;
  }
  .banner .title {
    max-width: 50%;
  }
  .banner p {
    max-width: 70%;
    font-size: 18px;
    line-height: 28px;
  }
}

@media (max-width: 991px) {
  .banner .banner-intro h1 {
    font-size: 36px;
    line-height: 46px;
  }
}

@media (max-width: 800px) {
  .banner {
    padding-top: 381px !important;
  }
  .banner .banner-img {
    height: 381px !important;
  }
  .banner .title {
    max-width: 350px;
  }
  .banner p {
    max-width: 300px;
  }
}

@media (max-width: 767px) {
  .banner .banner-intro h1 {
    font-size: 33px;
    line-height: 40px;
  }
}

@media (max-width: 640px) {
  .banner p {
    font-size: 16px;
    line-height: 24px;
  }
  .banner .title {
    max-width: 280px;
  }
  .banner p {
    max-width: 270px;
  }
}

.banner-inner {
  margin-top: 69px;
}

.banner-inner .banner-img {
  height: auto;
}

.banner-inner .banner-img::before {
  display: none;
}

.banner-inner .banner-img + .banner-intro {
  justify-content: flex-end;
}

.banner-inner .title {
  max-width: 100%;
}

.banner-inner .title.variant36 div {
  font-size: 36px;
  line-height: 48px;
  font-family: 'Graphik Medium Web', Arial;
}

.banner-inner .title.variant36 p {
  font-size: 36px;
  line-height: 40px;
  margin-top: 0;
}

@media (max-width: 1020px) {
  .banner-inner {
    padding-top: 350px !important;
  }
  .banner-inner .banner-img {
    height: 350px !important;
  }
}

@media (max-width: 800px) {
  .banner-inner {
    padding-top: 320px !important;
  }
  .banner-inner .banner-img {
    height: 320px !important;
  }
}

@media (max-width: 640px) {
  .banner-inner {
    margin-top: 58px;
  }
  .banner-inner .title.variant36 div {
    font-size: 28px;
    line-height: normal;
  }
}

.banner-narrow {
  margin: auto;
  max-width: 1200px;
  margin-top: 105px;
}

.banner-narrow .wrap {
  margin: auto 55px;
}

.banner-narrow img {
  width: 100%;
}

@media (max-width: 1380px) {
  .banner-narrow {
    margin: 105px 90px 0;
    width: auto;
  }
}

@media (max-width: 991px) {
  .banner-narrow {
    margin: 69px 0 0;
  }
}

@media (max-width: 640px) {
  .banner-narrow {
    margin-top: 58px;
  }
  .banner-narrow .wrap {
    margin: auto 20px;
  }
  .banner-narrow .title.variant36 p {
    font-size: 26px;
    line-height: 34px;
  }
}

.flex .text {
  width: 50%;
  padding-right: 65px;
}

.flex p {
  -webkit-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
}

.flex .pic {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

@media (max-width: 991px) {
  .flex .text {
    padding-right: 15px;
  }
}

@media (max-width: 640px) {
  .flex .text {
    padding: 0;
    width: 100%;
  }
  .flex .pic {
    width: 100%;
  }
}

.about {
  padding: 20px 0 80px;
  position: relative;
  overflow: hidden;
  z-index: 2;
}

.about .block-digits {
  width: 100%;
  display: flex;
  font-size: 16px;
  line-height: 24px;
  margin-top: 47px;
}

.about .block-digits > div {
  width: 33%;
}

.about .block-digits .digit {
  color: #fb7038;
  font-family: 'Graphik Medium Web', Arial;
  font-size: 36px;
  line-height: 48px;
}

.about .block-digits .digit + div {
  max-width: 200px;
}

@media (max-width: 991px) {
  .about {
    padding: 32px 0 65px;
  }
  .about h2 {
    margin: 5px 0 20px;
  }
  .about .pic {
    margin-top: 17px;
  }
  .about .pic img {
    width: 250px;
  }
  .about .block-digits {
    font-size: 14px;
    line-height: 20px;
    margin-top: 13px;
  }
  .about .block-digits .digit {
    font-size: 26px;
    line-height: 30px;
    margin-bottom: 5px;
  }
  .about .block-digits .digit + div {
    max-width: 185px;
  }
}

@media (max-width: 640px) {
  .about {
    padding: 30px 0 15px;
  }
  .about .pic {
    position: absolute;
    bottom: 90px;
    overflow: hidden;
    width: calc(100% - 20px);
